// Add slideDown animation to Bootstrap dropdown when expanding.
$('.dropdown').on('show.bs.dropdown', function() {
    $(this).find('.dropdown-menu').first().stop(true, true).slideDown();
});

// Add slideUp animation to Bootstrap dropdown when collapsing.
$('.dropdown').on('hide.bs.dropdown', function() {
    $(this).find('.dropdown-menu').first().stop(true, true).slideUp();
});

$('#form-index-lead').submit(function(e) {

    e.preventDefault();
    console.log('chegeu')
    $.ajax({
        type: 'POST',
        url: $('#form-index-lead').attr('action'),
        data: $('#form-index-lead').serialize(),
        success: function(response) {
            $('#form-index-lead')[0].reset();
            $('.msg-result-lead').html("Muito obrigado! Em breve entraremos em contato").fadeIn(0).fadeOut(6000);
        },
        error: function(response) {
            $('#form-index-lead')[0].reset();
            $('.msg-result-lead').html("Ocorreu um erro ao tentar enviar sua mensagem!").fadeIn(0).fadeOut(6000);
        },
    });

});